<template>
  <ul class="catalog__list">
      <ProductItem v-for= "product in products" :product="product" :key="product.id"/>
  </ul>
</template>

  <script>
  import ProductItem from './ProductItem.vue';

      export default {
          components: {ProductItem},
          props: ['products'],
          computed: {
          filteredProducts() {
            return this.products.filter(product => {
              const meetsPriceCriteria =
                (this.currentPriceFrom === 0 || product.price >= this.currentPriceFrom) &&
                (this.currentPriceTo === 0 || product.price <= this.currentPriceTo);
              const meetsCategoryCriteria =
                this.currentCategoryId === 0 || product.categoryId === this.currentCategoryId;
              return meetsPriceCriteria && meetsCategoryCriteria;
            });
          },
          }
      }
  </script>
  